import React from 'react'
import { assets } from '../assets/assets'
import Image from 'next/image'

const Hero = () => {
    return (
        <div className='h-screen flex flex-col items-center justify-center gap-14 bg-light text-center'>
            <h1 className='text-4xl md:text-5xl font-semibold'>Luxury Car Rental on Rent</h1>

            <form></form>

            <Image src={assets.main_restaurant} alt='hero' className='max-h-74' width={700} height={500} />
        </div>
    )
}

export default Hero
